import {updateObjectInArray} from '../utils/object-helper';
import {UsersType} from '../types/types';
import {ThunkAction} from 'redux-thunk';
import {AppStateType, BaseThunkType} from './Redux-store';
import {Dispatch} from 'react';
import {usersAPI} from '../API/users-API';

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET-USERS';
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE';
const SET_TOTAL_USERS_COUNT = 'SET-TOTAL-USERS-COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE-IS-FOLLOWING-PROGRESS';


let initialState = {
    users: [] as Array<UsersType>,
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: true,
    followingInProgress: [] as Array<number> //array of users id
}

export type InitialStateType = typeof initialState

const usersReducer = (state = initialState, action: ActionType): InitialStateType => {

    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, 'id', {followed: true})
            }
        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, 'id', {followed: false})
            }
        case SET_USERS: {
            return {...state, users: action.user}
        }
        case SET_CURRENT_PAGE: {
            return {...state, currentPage: action.currentPage}
        }
        case SET_TOTAL_USERS_COUNT: {
            return {...state, totalUsersCount: action.count}
        }
        case TOGGLE_IS_FETCHING: {
            return {...state, isFetching: action.isFetching}
        }
        case TOGGLE_IS_FOLLOWING_PROGRESS: {
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter((id: number) => id != action.userId)
            }
        }

        default:
            return state;
    }
}

//actionCreator
type ActionType = followSuccessType | unfollowSuccessType | setUsersACType | setCurrentPageACType |
    setTotalUsersCountACType | toggleIsFetchingACType | toggleFollowingProgressType

type followSuccessType = {
    type: typeof FOLLOW,
    userId: number
}
export const followSuccess = (userId: number): followSuccessType => ({type: FOLLOW, userId});

type unfollowSuccessType = {
    type: typeof UNFOLLOW,
    userId: number
}
export const unfollowSuccess = (userId: number): unfollowSuccessType => ({type: UNFOLLOW, userId});

type setUsersACType = {
    type: typeof SET_USERS,
    user: Array<UsersType>
}
export const setUsersAC = (user: Array<UsersType>): setUsersACType => ({type: SET_USERS, user});

type setCurrentPageACType = {
    type: typeof SET_CURRENT_PAGE,
    currentPage: number
}
export const setCurrentPageAC = (currentPage: number): setCurrentPageACType => ({type: SET_CURRENT_PAGE, currentPage});

type setTotalUsersCountACType = {
    type: typeof SET_TOTAL_USERS_COUNT,
    count: number
}
export const setTotalUsersCountAC = (totalUsersCount: number): setTotalUsersCountACType => ({
    type: SET_TOTAL_USERS_COUNT,
    count: totalUsersCount
});

type toggleIsFetchingACType = {
    type: typeof TOGGLE_IS_FETCHING,
    isFetching: boolean
}
export const toggleIsFetchingAC = (isFetching: boolean): toggleIsFetchingACType => ({
    type: TOGGLE_IS_FETCHING,
    isFetching
});

type toggleFollowingProgressType = {
    type: typeof TOGGLE_IS_FOLLOWING_PROGRESS,
    isFetching: boolean,
    userId: number
}
export const toggleFollowingProgress = (isFetching: boolean, userId: number): toggleFollowingProgressType => ({
    type: TOGGLE_IS_FOLLOWING_PROGRESS,
    isFetching,
    userId
});


//thunk
type DispatchType = Dispatch<ActionType>
type ThunkType = BaseThunkType<ActionType>

export const requestUsers = (page: number, pageSize: number): ThunkType => {
    return async (dispatch) => {
        dispatch(toggleIsFetchingAC(true));
        dispatch(setCurrentPageAC(page));

        let data = await usersAPI.getUsers(page, pageSize)
        dispatch(toggleIsFetchingAC(false));
        dispatch(setUsersAC(data.items));
        dispatch(setTotalUsersCountAC(data.totalCount));
    }
}

const followUnfolowFlow = async (dispatch: DispatchType, UserId: number, apiMethod: any, actionCreator: (userId:number) =>unfollowSuccessType| followSuccessType) => {
    dispatch(toggleFollowingProgress(true, UserId));
    let response = await apiMethod(UserId)
    if (response.data.resultCode === 0) {
        dispatch(actionCreator(UserId))
    }
    dispatch(toggleFollowingProgress(false, UserId));
}


export const follow = (UserId: number): ThunkType => {
    return async (dispatch) => {
        let apiMethod = usersAPI.follow.bind(usersAPI);
        await followUnfolowFlow(dispatch, UserId, apiMethod, followSuccess)
    }
}
export const unfollow = (UserId: number): ThunkType => {
    return async (dispatch) => {
        let apiMethod = usersAPI.unfollow.bind(usersAPI);
        await followUnfolowFlow(dispatch, UserId, apiMethod, unfollowSuccess)
    }
}

export default usersReducer;