import React from "react";
import s from './Posts.module.css'

type PostType ={
    message: string;
    likeCount: number;
}
const Post = (props: PostType) => {

    return (
        <div className={s.item}>

            <img src='data:image/jpeg;base64,'/>
            {props.message}
            <img src='https://pngimg.com/uploads/like/like_PNG64.png'/>
            {props.likeCount}
        </div>
    )
}

export default Post;