import React from 'react';
import {getAuthUserData} from './Auth-reducer';
import {ThunkAction} from 'redux-thunk';
import {AppStateType} from './Redux-store';

const SET_INITIALIZED_SUCCESS = 'SET-INITIALIZED-SUCCESS';

let initialState  = {
    initialized: false
}
export type initialStateType = typeof initialState

const appReducer = (state = initialState, action: initializedSuccessActionType):initialStateType => {

    switch (action.type) {
        case SET_INITIALIZED_SUCCESS:
            return {
                ...state,
                initialized: true,
            }
        default:
            return state;
    }
}

export  type initializedSuccessActionType = {
    type: typeof  SET_INITIALIZED_SUCCESS
}

export const initializedSuccess = ():initializedSuccessActionType => ({type: SET_INITIALIZED_SUCCESS});

//thunk

type ThunkType = ThunkAction<Promise<void>,AppStateType,unknown,initializedSuccessActionType>

export const initializeApp = () => (dispatch: any) => {
    let promise = dispatch(getAuthUserData())
    Promise.all([promise])
        .then(() => {
        dispatch(initializedSuccess())
    })

}

export default appReducer;