import React from 'react';
import {connect} from 'react-redux';
import {follow, requestUsers, unfollow} from '../../redux/Users-reducer';
import Users from './Users';
import Preloader from '../Common/Preloader/Preloader';
import {compose} from 'redux';
import {
    currentPage,
    followingInPropgress,
    getUsers,
    isFetching,
    pageSize,
    totalUsersCount
} from '../../redux/Users-selectors';
import {UsersType} from '../../types/types';
import {AppStateType} from '../../redux/Redux-store';

type MapStateUsersContainerType = {
    currentPage: number
    pageSize: number
    isFetching:boolean
    totalUsersCount:number
    users: Array<UsersType>
    followingInProgress: Array<number>
}
type MapDispatchUsersContainerType = {
    getUsers: (pageNumber:number,pageSize:number)=>void
    unfollow: (userId:number) => void
    follow: (userId:number) => void

}

type UsersContainerType = MapStateUsersContainerType & MapDispatchUsersContainerType

class UsersContainer extends React.Component<UsersContainerType> {
    componentDidMount() {
        this.props.getUsers(this.props.currentPage , this.props.pageSize)
    }

    onPageChanged = (pageNumber: number) => {
        this.props.getUsers(pageNumber , this.props.pageSize)
    }

    render() {

        return <>
            {this.props.isFetching ? <Preloader/> : null}

            <Users totalUsersCount={this.props.totalUsersCount}
                   pageSize={this.props.pageSize}
                   currentPage={this.props.currentPage}
                   onPageChanged={this.onPageChanged}
                   users={this.props.users}
                   follow={this.props.follow}
                   unfollow={this.props.unfollow}
                   followingInProgress={this.props.followingInProgress}
            />
        </>
    }
}

let mapStateProps = (state: AppStateType):MapStateUsersContainerType => {
    return {
        users: getUsers(state),
        pageSize: pageSize(state),
        totalUsersCount: totalUsersCount(state),
        currentPage: currentPage(state),
        isFetching: isFetching(state),
        followingInProgress: followingInPropgress(state)
}
}


export default compose<React.ComponentType>(
    connect<MapStateUsersContainerType,
        MapDispatchUsersContainerType,
        UsersContainerType,
        AppStateType>(mapStateProps, {
        follow,
        unfollow,
        getUsers: requestUsers
    })
) (UsersContainer)

