import {ResultCodesEnum} from '../API/API';
import {FormAction, stopSubmit} from 'redux-form';
import {BaseThunkType} from './Redux-store';
import {authAPI} from '../API/auth-API';

const SET_USER_DATA = 'SET-USER-DATA';

type initialStateType = {
    userId: number | null,
    email: string | null,
    login: string | null,
    isAuth: boolean
}

let initialState: initialStateType = {
    userId: null,
    email: null,
    login: null,
    isAuth: false
}

const authReducer = (state = initialState, action: setAuthUserDataType): initialStateType => {

    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
            }
        default:
            return state;
    }
}

export type setAuthUserDataPayloadType = {
    userId: number | null,
    email: string | null,
    login: string | null
    isAuth: boolean
}

export type setAuthUserDataType = {
    type: typeof SET_USER_DATA
    payload: setAuthUserDataPayloadType
}
export const setAuthUserData = (userId: number | null,
                                email: string | null,
                                login: string | null,
                                isAuth: boolean): setAuthUserDataType => ({
    type: SET_USER_DATA, payload: {userId, email, login, isAuth}
});

//thunk

type ThunkType = BaseThunkType<setAuthUserDataType | FormAction>

export const getAuthUserData = (): ThunkType => async (dispatch) => {
    let MeData = await authAPI.me()
    if (MeData.resultCode === ResultCodesEnum.Success) {
        let {id, email, login} = MeData.data;
        dispatch(setAuthUserData(id, email, login, true));
    }
}

export const login = (email: string, password: string, rememberMe: boolean): ThunkType => async (dispatch) => {
    // создаём переменную респонс, результат которым зарезолвится промис.
    let LoginData = await authAPI.login(email, password, rememberMe)
    if (LoginData.resultCode === ResultCodesEnum.Success) {
        await dispatch(getAuthUserData());
    } else {
        let message = LoginData.messages.length > 0 ? LoginData.messages[0] : 'Some error'
        dispatch(stopSubmit('login', {_error: message}))
    }
}

export const logout = (): ThunkType => async (dispatch) => {
    let response = await authAPI.logout()
    if (response.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false));
    }
}

export default authReducer;